import { adapters } from "@rp/adapters"

type PromptMobileWebCustomerIo = {
  name: "getAppPromotion"
  data: {
    action: "getApp" | "dismiss" | "promptShown"
    promptType: "banner" | "modal"
    URL?: string
  }
}

const promptMobileWebCustomerIo = {
  sendPromptMobileWebInteraction(data: PromptMobileWebCustomerIo["data"]) {
    const message: PromptMobileWebCustomerIo = {
      name: "getAppPromotion",
      data,
    }
    adapters.customerIo.track(message)
  },
}

export { type PromptMobileWebCustomerIo, promptMobileWebCustomerIo }
